import { useState } from "react";
import {
  Button,
  Card,
  Col,
  Container,
  Form,
  ListGroup,
  Modal,
  Row,
} from "react-bootstrap";

function Cards() {
  const [taskList, setTaskList] = useState([
    { name: "Derrick", list: ["Derrick Lorem Ipsum 2"] },
    { name: "Maxwell", list: ["Maxwell Lorem Ipsum", "Maxwell Lorem Ipsum 2"] },
    { name: "Zazza", list: ["Zazza Lorem Ipsum", "Zazza Lorem Ipsum 2"] },
    { name: "Sam", list: ["Sam Lorem Ipsum", "Sam Lorem Ipsum 2"] },
  ]);
  const [showAddModal, setShowAddModal] = useState(false);
  const [newTask, setNewTask] = useState("");
  const [boardToEdit, setBoardToEdit] = useState(null);
  const [itemToEdit, setItemToEdit] = useState(null);
  const [isTaskEdit, setIsTaskEdit] = useState(false);
  const [toReplace, setToReplace] = useState([]);
  const handleClose = (addValue) => {
    if (addValue) {
      if (!newTask) return;
      if (isTaskEdit) {
        let updateTask = taskList;
        updateTask[boardToEdit].list[itemToEdit] = newTask;
        setTaskList(updateTask);
        setNewTask("");
      } else {
        let updateTask = taskList;
        updateTask[boardToEdit].list.push(newTask);
        setTaskList(updateTask);
        setNewTask("");
      }
    }
    setShowAddModal(false);
  };
  const handleShow = (index) => {
    setBoardToEdit(index);
    setNewTask("");
    setIsTaskEdit(false);
    setShowAddModal(true);
  };
  const onDragOver = (e) => {
    e.preventDefault();
  };
  const onDragStart = (e, board, index) => {
    setToReplace([board, index]);
  };
  const editTask = (board, index) => {
    setBoardToEdit(board);
    setItemToEdit(index);
    setIsTaskEdit(true);
    setNewTask(taskList[board].list[index]);
    setShowAddModal(true);
  };
  const updateValues = (val) => {
    setTimeout(() => {
      setTaskList(val);
    }, 10);
    setToReplace([]);
  };
  const onDrop = (e, id) => {
    e.preventDefault();
    let updateTask = taskList;
    let removed = updateTask[toReplace[0]].list.splice(toReplace[1], 1);
    updateTask[id].list.push(removed[0]);
    updateValues(updateTask);
  };
  return (
    <div>
      <Container fluid="true">
        <Row className="py-5 justify-content-center">
          <Col sm="11">
            <Row className="justify-content-center">
              {taskList.map((task, index) => (
                <Col
                  md="9"
                  onDragOver={(e) => onDragOver(e)}
                  className="mb-5"
                  lg="6"
                  xl="3"
                  key={"tasks" + index}
                >
                  <Card className="shadow border-0 rounded-0">
                    <Card.Body>
                      <Card.Title>{task.name}</Card.Title>
                      <ListGroup
                        variant="flush"
                        onDropCapture={(e) => {
                          onDrop(e, index);
                        }}
                      >
                        {task.list.length > 0 && (
                          <div>
                            {task.list.map((item, i) => (
                              <ListGroup.Item
                                className="c-grab"
                                onDragStart={(e) => {
                                  onDragStart(e, index, i);
                                }}
                                draggable="true"
                                key={i}
                              >
                                <div className="d-flex justify-content-between">
                                  <div>
                                    <img
                                      alt="grip line"
                                      draggable="false"
                                      width="10"
                                      className="mr-3"
                                      src="data:image/png;base64,"
                                    />
                                    {item}
                                  </div>
                                  <div className="c-pointer">
                                    <img
                                      src="data:image/png;base64,"
                                      draggable="false"
                                      alt="edit"
                                      width="10"
                                      title="edit"
                                      onClick={() => {
                                        editTask(index, i);
                                      }}
                                    />
                                  </div>
                                </div>
                              </ListGroup.Item>
                            ))}
                          </div>
                        )}
                        {task.list.length === 0 && (
                          <ListGroup.Item onClick={() => handleShow(index)}>
                            Add or Drop Item
                          </ListGroup.Item>
                        )}
                      </ListGroup>
                      <Button
                        variant="secondary"
                        className="rounded-0 mt-3"
                        size="sm"
                        block
                        onClick={() => handleShow(index)}
                      >
                        Add Task
                      </Button>
                    </Card.Body>
                  </Card>
                </Col>
              ))}
            </Row>
          </Col>
        </Row>
      </Container>
      <Modal
        className="rounded-0"
        show={showAddModal}
        onHide={() => {
          handleClose(false);
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title>
            {isTaskEdit
              ? `Edit ${taskList[boardToEdit]?.name}'s task`
              : `Add task to ${taskList[boardToEdit]?.name}`}
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group>
            <Form.Label>Task</Form.Label>
            <Form.Control
              value={newTask}
              onChange={(e) => setNewTask(e.target.value)}
              type="text"
            />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="primary"
            block
            className="rounded-0"
            onClick={() => {
              handleClose(true);
            }}
          >
            {isTaskEdit ? `Edit task` : `Add task`}
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default Cards;
